
$font-circular: 'Circular', sans-serif
$font-circularMedium: 'Circular-Medium', sans-serif
$font-circularItaic: 'Circular-italic', sans-serif

$font-GilroyBold: 'Gilroy-Bold', sans-serif
$font-GilroyItalic: 'Gilroy-italic', sans-serif
$font-GilroyExtra: 'Gilroy-Extrabold', sans-serif
$font-GilroySemibold: 'Gilroy-Semibold', sans-serif

$font-Playfair: 'Playfair', sans-serif


$font-weight-thin: 100           // Thin (Hairline)
$font-weight-extra-light: 200    // Extra Light (Ultra Light)
$font-weight-light: 300          // Light
$font-weight-normal: 400         // Regular (normal)
$font-weight-medium: 500         // Medium
$font-weight-semi-bold: 600      // Semi Bold (Demi Bold)
$font-weight-bold: 700           // Bold
$font-weight-extra-bold: 800    // Extra Bold (Ultra Bold)
$font-weight-black: 900        // Black (Heavy)

//Gastro colors
$subtext-color: #3F3232
$text-color: #2E2E2E
$suptitle-color: #FF9401
$text-btn-color: #FFFFFF
$rating-color: #818181
$cartbtn-color: #696969

html
  font-family: $font-circular

button
  border: 0

ol, ul 
  list-style: none

a 
  text-decoration: none

p, ul, li, h1, h2
  margin: 0
  padding: 0

* 
  box-sizing: border-box

img 
  width: 100%
  height: auto

.container
  width: 100%
  max-width: 1225px
  margin: auto
  padding: 0 15px

.title
  font-family: $font-GilroyBold
  font-size: 48px
  line-height: 60px
  color: #2E2E2E

.suptitle
  font-family: $font-GilroySemibold
  font-weight: $font-weight-semi-bold
  font-size: 24px
  line-height: 30px
  color: $suptitle-color

.description-text
  font-family: $font-circular
  font-size: 20px
  line-height: 160%
  color: $subtext-color

.button
  width: 243px
  background: $suptitle-color
  border-radius: 31px
  filter: drop-shadow(0px 20px 40px rgba(255, 148, 1, 0.2))


