.header
  position: relative

.header__wrap
  display: flex
  justify-content: space-between
  padding-top: 4%
  margin-bottom: 11.5%

.header__logo
  width: 110px

.header__logo-img
.header__actions
  display: flex

.header__search
  width: 24px
  margin-right: 62px

.header__search-img
.header__cart
  width: 24px
  margin-right: 62px

.header__cart-img
.header__menu-burger
  width: 24px

.header__menu-img
.header__decor
  position: absolute
  width: 250px
  height: 175px
  top:0
  left: 50%
  transform: translate(-50%, 0)
  background: url('../images/sandwich.png')