.about
  padding-top: 20px
  padding-bottom: 6.3%
  background: linear-gradient(180deg, rgba(255, 148, 1, 0.05) 0%, rgba(255, 237, 211, 0) 100%)

.about__wrap
  display: flex
  align-items: center
  justify-content: space-between

.about__image
  width: 670px
  height: 580px
  background-image: url('../images/background-about.png')
  background-repeat: no-repeat
  background-position: 90%
  position: relative

.about__video-link
  display: block
  width: 80px
  height: 80px
  position: absolute
  top: 62%
  left: 83%

.about__video-btn
.about__text
  width: 426px

.about__suptitle
  margin-bottom: 24px

.about__title
  font-family: $font-GilroyBold
  font-size: 52px
  line-height: 60px
  color: $text-color
  margin-bottom: 21px

.about__description
  margin-bottom: 48px

.about__link-btn
  text-align: center
  padding: 16px 32px
  color: $text-btn-color
  &:hover
    color: $text-color