h1
	display: none

.restaurant
	padding-bottom: 5.4%

.restaurant__wrap
	display: flex
	justify-content: space-between
	align-items: center

.restaurant__text
	width: 100%
	max-width: 550px

.restaurant__suptitle
	padding-left: 29px
	margin-bottom: 16px
	&::before
		content: ""
		position: relative
		background: $suptitle-color
		border-radius: 0.5px
		display: block
		width: 24px
		height: 1px
		top: 23px
		left: -27px
	
.restaurant__title
	position: relative
	font-family: $font-GilroyBold
	font-size: 96px
	line-height: 108px
	margin-bottom: 33px
	color: $text-color

.restaurant__title-food,
.restaurant__title-mood
	position: relative
	display: inline-block

.restaurant__title-food::before,
.restaurant__title-food::after
	display: block
	position: absolute
	content: ""

.restaurant__title-food::before
	background: url('../images/letter-image.png') no-repeat
	width: 60px
	height: 60px
	top: 32%
	left: 13%

.restaurant__title-food::after
	background: url('../images/letter-img.png') no-repeat
	width: 136px
	height: 80px
	top: 14%
	left: 3%

.restaurant__title-mood::before,
.restaurant__title-mood::after
	display: block
	position: absolute
	content: ""
	background: url('../images/Mood.png') no-repeat
	width: 55px
	height: 55px
	top: 35%
	left: 32%
	background-color: $text-btn-color

.restaurant__title-mood::after
	left: 55%

.restaurant__description
	margin-bottom: 44px
	
.restaurant__menu-btn
	margin-bottom: 60px

.restaurant__menu-link
	text-align: center
	display: block
	font-family: $font-circular
	font-weight: 500
	font-size: 20px
	line-height: 30px
	text-transform: capitalize
	padding: 16px 32px
	color: $text-btn-color
	&:hover
		color: $text-color

.restaurant__socials
	max-width: 165px
	filter: drop-shadow(0px 10px 10px rgba(255, 148, 1, 0.2))
	display: flex
	justify-content: space-between

.restaurant__socials-link
	width: 35px
	height: 35px

.restaurant__socials-img
	width: 100%
	&:hover
		filter: invert(100%)

.restaurant__picture
	width: 600px
	height: 600px
	background-image: url('../images/restaurant-image.png')
	background-repeat: no-repeat
	background-position: -280px -50px
	


	