.testimonial
	padding: 7% 0

.testimonial__wrap
	display: flex
	justify-content: space-between
	align-items: center

.testimonial__text
	max-width: 360px
	width: 100%

.testimonial__suptitle
	margin-bottom: 23px

.testimonial__title
	margin-bottom: 30px

.testimonial__slider
.testimonial__slide
.testimonial__slide-text
	font-family: $font-circularItaic
	font-style: italic
	font-weight: 500
	font-size: 18px
	line-height: 160%
	color: $subtext-color
	margin: 0
	margin-bottom: 45px

.testimonial__person
	display: flex
	justify-content: space-between
	align-items: center

.testimonial__person-wrap
.testimonial__slide-name
	font-family: $font-GilroySemibold
	font-size: 24px
	line-height: 30px
	color: $text-color
	margin-bottom: 4px

.testimonial__slide-persone
	font-family: $font-circular
	font-style: normal
	font-size: 14px
	line-height: 22px
	color: #675959

.testimonial__slide-control
	width: 51px
	height: 31px

.testimonial__left
	width: 100%

.testimonial__pic
	width: 612px
	height: 580px
	background-image: url('../images/testimonial.png')
	background-repeat: no-repeat
	background-size: cover

.testimonial__slider .swiper-button-prev
	right: 9%
	top: 87%
	left: auto

.testimonial__slider .swiper-button-next
	right: 1px
	top: 87%

.testimonial__slider .swiper-button-disabled.swiper-button-prev,
.testimonial__slider .swiper-button-disabled.swiper-button-next
	opacity: 1

.testimonial__slider .swiper-button-prev::after,
.testimonial__slider .swiper-button-next::after
	display: block
	position: absolute
	content: ""
	background: url('../images/arrow-active.png') no-repeat center
	width: 32px
	height: 32px

.testimonial__slider .swiper-button-prev::after
	transform: rotate(180deg)

.testimonial__slider .swiper-button-disabled.swiper-button-prev::after,
.testimonial__slider .swiper-button-disabled.swiper-button-next::after
	background: url('../images/arrow.png') no-repeat center
	width: 14px
	height: 14px

.testimonial__slider .swiper-button-disabled.swiper-button-next::after
	transform: rotate(180deg)

.testimonial__slider .swiper-button-disabled.swiper-button-prev::after
	transform: none