.take-away
	position: relative
	padding-top: 8.5%
	padding-bottom: 2%

.take-away__wrap
	display: flex
	justify-content: space-between
	align-items: center
	flex-wrap: wrap

.take-away__picture
	width: 625px
	height: 540px
	background-image: url('../images/imageMobile.png')
	background-repeat: no-repeat
	background-size: cover
	background-position: -20px 0

.take-away__text
	width: 100%
	max-width: 445px

.take-away__suptitle
	margin-bottom: 23px

.take-away__title
	margin-bottom: 18px

.take-away__description
	margin-bottom: 40px

.take-away__action
	display: flex

.take-away__btn
	width: 149px
	height: 49px
	margin-right: 18px

.take-away__img
.take-away__decor1
	position: absolute
	width: 290px
	height: 355px
	top: 0
	left: 0
	background: url('../images/7.png') no-repeat center

.take-away__decor2
	position: absolute
	width: 380px
	height: 300px
	top: 0
	right: 0
	background: url('../images/4.png') no-repeat center



