.footer
	position: relative
	background: linear-gradient(180deg, rgba(255, 148, 1, 0.05) 0%, rgba(255, 237, 211, 0) 100%)
	padding-top: 4%
	padding-bottom: 2.3%

.footer__wrap
	width: 100%
	max-width: 1130px
	margin-left: auto
	
.footer__content
	display: flex
	justify-content: space-between
	margin-bottom: 6.5%

.footer__info

.footer__logo
	width: 110px
	margin-bottom: 20px
	
.footer__logo-img
.footer__email
	display: flex
	margin-bottom: 20px
	align-items: center

.footer__email-pic
	width: 19px
	height: 12px
	margin-right: 13px

.footer__email-img
.footer__email-name
	font-family: $font-circular
	font-size: 16px
	line-height: 30px
	text-transform: capitalize
	color: #3F3232

.footer__site
	display: flex

.footer__site-pic
	width: 19px
	height: 12px
	margin-right: 13px

.footer__web-img
.footer__site-name
	font-family: $font-circular
	font-size: 16px
	line-height: 30px
	color: #3F3232

.footer__text
	width: 100%
	max-width: 760px
	display: flex
	align-items: baseline
	justify-content: space-between

.footer__list
	width: 140px
	&:last-child
		margin-right: 0

.footer__list-title
	font-family: $font-GilroySemibold
	font-size: 20px
	line-height: 30px
	text-transform: capitalize
	color: $text-color
	margin-bottom: 18px

.footer__list-item
	font-family: $font-circular
	font-size: 18px
	line-height: 30px
	text-transform: capitalize
	color: #6A5C5C
	margin-bottom: 19px

.footer__social-link
	font-family: $font-circularMedium
	font-size: 16px
	line-height: 30px
	text-decoration-line: underline
	text-transform: capitalize
	color: $text-color

.footer__copyrights
	font-size: 12px
	text-align: center

.footer__heart
	display: inline-block
	width: 11px
	height: 11px
	background: url('../images/heart-sm.png') no-repeat 0 0
	margin: 0 5px

.footer__decor1
	position: absolute
	width: 105px
	height: 100px
	top: -10%
	right: 0
	background: url('../images/heart.png') no-repeat center

.footer__decor2
	position: absolute
	width: 255px
	height: 275px
	top: 9%
	left: 0
	background: url('../images/pineapple.png') no-repeat center