.recipes
	position: relative
	background: linear-gradient(180deg, rgba(255, 148, 1, 0.05) 0%, rgba(255, 237, 211, 0) 100%)
	padding-top: 5%
	padding-bottom: 20px

.recipes__wrap
	text-align: center

.recipes__suptitle
	margin-bottom: 13px

.recipes__title
	margin-bottom: 6%

.recipes__cards
	display: flex
	justify-content: space-between
	align-items: center
	flex-wrap: wrap
	margin-bottom: 6%

.recipes__card
	width: 100%
	max-width: 354px
	background: $text-btn-color
	border-radius: 8px
	padding: 12px
	filter: drop-shadow(0px 40px 80px rgba(255, 148, 1, 0.15))

.recipes__card-pic
	height: 230px
	margin-bottom: 35px

.recipes__card-img
	width: 100%

.recipes__card-info
	display: flex
	justify-content: space-between
	align-items: center
	margin-bottom: 20px

.recipes__card-name
	font-family: $font-circularMedium
	font-size: 20px
	color: $text-color

.recipes__card-rating
	font-family: $font-GilroySemibold
	font-size: 16px
	color: $rating-color
	position: relative
	&::before
		display: block
		position: absolute
		content: ""
		background: url('../images/Star.png') no-repeat
		width: 11px
		height: 11px
		top: 22%
		left: -60%

.recipes__card-btn
	width: 110px
	background: rgba(196, 196, 196, 0.26)
	border-radius: 15px
	padding: 10px
	font-family: $font-circularMedium
	font-size: 10px
	line-height: 1
	color: $cartbtn-color

.recipes__card-price
	font-family: $font-GilroyBold
	font-size: 18px
	color: $suptitle-color

.recipes__btn
	margin-bottom: 26px

.recipes__link
	display: block
	text-align: center
	padding: 16px 30px
	color: $text-btn-color
	margin: 0 auto
	filter: drop-shadow(0px 20px 40px rgba(255, 148, 1, 0.2))
	&:hover
		color: $text-color

.recipes__decor
	position: absolute
	width: 85px
	height: 95px
	top: 12%
	right: 28%
	background: url('../images/1.png') no-repeat center