.features
	position: relative
	padding-top: 5%
	padding-bottom: 7.3%

.features__wrap
	width: 100%
	max-width: 970px
	margin: 0 auto
	text-align: center

.features__suptitle
	margin-bottom: 13px

.features__title
	margin-bottom: 43px

.features__cards
	display: flex
	justify-content: space-between
	align-items: center
	flex-wrap: wrap

.features__card
	width: 245px
	padding: 10px 0
	text-align: center

.features__card-pic
	width: 100px
	height: 116px
	margin: auto
	margin-bottom: 25px

.features__card-img
.features__card-title
	font-family: $font-GilroyBold
	font-size: 24px
	line-height: 30px
	color: $text-color
	margin-bottom: 19px

.features__card-text
	font-family: $font-circular
	font-size: 18px
	line-height: 160%
	color: $subtext-color
	overflow: hidden
	display: -webkit-box
	-webkit-line-clamp: 3
	-webkit-box-orient: vertical

.features__decor1
	position: absolute
	width: 190px
	height: 230px
	top: 0
	left: 0
	background: url('../images/2.png') no-repeat

.features__decor2
	position: absolute
	width: 260px
	height: 320px
	top: 0
	right: 0
	background: url('../images/3.png')